.assign {
  max-width: 1200px;
  margin: 64px auto;
}

.assign h5 {
  color: rgb(47, 49, 50);
  font-size: 20px;
  font-weight: 300;
  padding-bottom: 16px;
  border-bottom: 1px solid var(--border-color);
}

@media screen and (max-width: 1024px) {
  .assign {
    padding: 0 8px;
  }
}
